@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-about {
  background-color: var(--bg-container);
  max-width: 90%;
  max-height: 100%;
  border-radius: 5px;
  overflow: hidden;
  padding: 1rem;
  margin: .5rem auto;
  z-index: 20;
}

.container-about-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.container-about-image::before {
  content: '';
  display: flex;
  width: 50%;
  height: 100%;
  background-image: linear-gradient(to right, var(--bg-title) , var(--bg-gradients));
  position: absolute;
  left: 0;
  z-index: -1;
}

.container-about-image img {
  max-width: 100%;
  max-height: 100%;
  padding: .4rem;
}

.container-about-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: .4rem;
  margin: .2rem auto;
}

.container-about-title {
  width: 100%;
  margin-bottom: .5rem;
}

header h1 {
  letter-spacing: 1px;
}

.container-about-title::before {
  content: '';
  width: 70px;
  height: 30px;
  position: absolute;
  background-image: linear-gradient(to right, var(--bg-title) 0%, var(--bg-gradients) 100%);
  display: flex;
  z-index: -1;
}

.container-about-info p {
  letter-spacing: 1px;
  margin-bottom: .5rem;
}

.container-about-btn {
  background-image: linear-gradient(to right, var(--bg-title) 0%, var(--bg-gradients) 100%);
  letter-spacing: 1px;
  color: var(--color);
  border-radius: 20px;
  border: none;
  outline: none;
  width: 40%;
  height: 2.5rem;
  cursor: pointer;
  overflow: hidden;
  padding: 5px;
}


@media screen and (min-width: 1000px) {
  .container-about {
    display: flex;
    justify-content: center;
    align-content: center;
    max-height: 400px;
    max-width: 500px;
  }
  .container-about-info {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-content: center;
  }
  .container-about-image {
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
  }
  .container-about-image img {
    height: 100%;
  }
  .container-about-image::before {
    width: 70px;
    height: 100%;
  }
  .container-about-info p {
    overflow: auto;
  }
  .container-about-btn {
    padding: 5px;
  }
  .container-about-info p::-webkit-scrollbar {
    width: 4px;     /* Tama単o del scroll en vertical */
    height: 1px;    /* Tama単o del scroll en horizontal */
    background-color: var(--bg-container);
    border-radius: 50px;
  }
  .container-about-info p::-webkit-scrollbar-thumb {
    background-color: var(--bg-title);
    border-radius: 8px;
  }
  .container-about-info::-webkit-scrollbar-thumb:hover {
    background: #b3b3b3;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
  }
}
:root {
  --bg-all: #FFFCEA;
  --bg-container: #ffe0bd;
  --bg-title: #fcb242;
  --bg-gradients: #f5c7de;
  --color: #0e191b;
}
/* ** FONTS ** */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--bg-all);
  font-family: 'Source Sans Pro', sans-serif;
  color: var(--color);
}

.wrapper {
  display: grid;
}

@media screen and (min-width: 600px) {
  .wrapper {
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }
}

@media screen and (min-width: 1000px) {
  .wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .wrapper {
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    padding: 5px;
  }
}
